.NoteItem {
    background-color: #D5C9A9;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
    max-height: 140px;
    max-width: 740px;
}

.NoteItemExpanded {
    background-color: #D5C9A9;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
}

.NoteItemTextContainer {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2; /* lines of the text to be showed */
}

.NoteItemTextContainerExpanded {
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

.NoteItemEditIcon {
    float: right;
}

.NoteItemEditIcon .material-symbols-outlined:hover {
    color: #D6D1C6;
}

.NoteItemCloseMark {
    float: right;
}

.NoteItemCloseMark .material-symbols-outlined:hover {
    color: #D6D1C6;
}

.NoteItemExpandMark {
    float:right;
}

.NoteItemExpandMarkHidden {
    visibility: hidden;
}